import React , { useState } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import css from '../styles/contacts.module.scss'
import Slide from 'react-reveal/Slide';
import { request } from 'graphql-request'
 
const mutation = `mutation SEND_PORTFOLIO_EMAIL($message: String! , $name:String! , $email:String!) {
  sendPortfolioEmail(message: $message , email:$email , name:$name ) {
    status
  }
}`

const initialState = {
    name:"",
    email:"",
    message:""
}


export default () => {

 const [ form , setForm ] = useState(initialState)

 const [ success , setSuccess ] = useState(false)
const [error , setError] = useState(false)
 const [ loading , setLoading ] = useState(false)


 const { name , email , message } = form

const sendMessage =  e => {
    setError(false)
    setSuccess(false)
    e.preventDefault()
    setLoading(true)
     request('https://ok-kononov.herokuapp.com/graphql', mutation, { message , email , name }).then(() => {
      setForm(initialState)
      setSuccess(true)
      setLoading(false)
  }).catch(error =>  {
    setLoading(false)
    setError(error.response.errors[0].message)
      console.log(error.response.errors[0].message) 
    })
}

 const handleForm = e => {
     setForm({...form , [e.target.name]:e.target.value})
 }


    return (
        <Layout heading = 'CONTACTS'>
            <SEO    title = 'contacts' />
            <div className = {css.wrapper}>
               <div>
                   <p style = {{color:'rgb(27,142,153)'}}>ANTON SAVYTSKYI</p>
                   <p>+1(949)232-6088</p>
                   <p>Toxasava@icloud.com</p>

               </div>
                <iframe title = 'address' src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3018.604737962208!2d-73.94109488428482!3d40.836644837917056!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c2f682df62d355%3A0x8919bc027b4f45a!2s2085%20Amsterdam%20Ave%2C%20New%20York%2C%20NY%2010032!5e0!3m2!1sen!2sus!4v1576892215891!5m2!1sen!2sus"  frameBorder="0"  allowFullScreen=""></iframe>
               <Slide up>
               <form onSubmit = {(e) => sendMessage(e)}>
                   <h2>LEAVE A MESSAGE</h2>
                    <input autoComplete="true" value = {email} minLength = '6' onChange = {e => handleForm(e)} type = 'email' name = 'email' placeholder = 'your email' />
                    <input autoComplete="true" value = {name} minLength = '3' onChange = {e => handleForm(e)} type = 'text' name = 'name' placeholder = 'your name' />
                    <textarea value = {message} placeholder = 'Text a message' onChange = {e => handleForm(e)} type = 'text' name = 'message' minLength = '15' />
                    {success && <p style = {{ marginBottom:'10px' , textAlign:'center' }}>Message sent!</p>}
                    {error && <p style = {{ color:'red' , marginBottom:'10px' , textAlign:'center' }}>{error}</p>}
                    <button disabled = {!name || !email || !message} type = 'submit' >{loading ? 'Loading...' : 'SEND'}</button>
               </form>
               </Slide>
            </div>
        </Layout>
    )
}